.bsxui-d-flex {
  display: flex;
}

.bsxui-mt-3 {
  margin-top: 12px !important;
}

.bsxui-mt-4 {
  margin-top: 24px !important;
}

.bsxui-mb-3 {
  margin-bottom: 12px !important;
}

.bsxui-mb-4 {
  margin-bottom: 24px !important;
}

.bsxui-ml-auto {
  margin-left: auto !important;
}

.bsxui-px-0 {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.bsxui-p-0 {
  padding: 0 !important;
}

.bsxui-pt-0 {
  padding-top: 0 !important;
}

.bsxui-pb-0 {
  padding-bottom: 0 !important;
}

.bsxui-bt-0 {
  border-top: 0 !important;
}

.bsxui-bb-0 {
  border-bottom: 0 !important;
}

.bsxui-w-100 {
  width: 100% !important;
}

.bsxui-w-50 {
  width: 50% !important;
}

.bsxui-h-auto {
  height: auto !important;
}

.bsxui-va-middle {
  vertical-align: middle !important;
}

.bsxui-text-danger {
  color: #ca4a1f !important;
}

.bsxui-border-danger {
  border-color: #ca4a1f !important;
}

.bsxui-bg-danger {
  background-color: #ca4a1f !important;
}

.bsxui-disabled {
  pointer-events: none !important;
}

.bsxui-alert {
  color: inherit;
  background: #fef8ee;
  border: 1px solid rgb(249.75, 218.25, 165.75);
  border-radius: 0.25rem;
  font-size: 13px;
  padding: 0.125rem 0.25rem;
}

.wp-block {
  max-width: unset !important;
}

[data-type]:not(:focus):not(.is-selected):hover:after {
  content: "";
  z-index: 100;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  color: unset;
  background-color: unset;
  font-size: unset;
  border: 1px dashed var(--wp-admin-theme-color);
  padding: unset;
  pointer-events: none;
}
[data-type]:not(:focus):not(.is-selected):hover:before {
  content: attr(data-title);
  z-index: unset;
  position: absolute;
  top: unset;
  bottom: 100%;
  left: 0;
  right: unset;
  display: block;
  color: #ffffff;
  background-color: var(--wp-admin-theme-color);
  font-size: 9px;
  padding: 1px 4px;
  pointer-events: none;
  width: auto;
  white-space: nowrap;
  font-family: inherit;
  margin-left: 0;
  line-height: 1.5;
}

a.bsxui-link {
  color: var(--wp-admin-theme-color);
}
a.bsxui-link:hover, a.bsxui-link:focus {
  color: var(--wp-admin-theme-color);
  text-decoration: underline;
}

.bsxui-isselected-config-panel {
  background: #ffffff;
  border: 1px solid #dee2e6;
  margin-top: 0.5rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.bsxui-img-upload-placeholder {
  background: rgba(238, 238, 238, 0.5);
  padding: 0.5rem;
}

.bsxui-upload-btn-wrapper {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.upload-img {
  max-width: 100%;
  height: auto;
}

.bsxui-banner-button {
  height: auto;
  padding: 0 !important;
  border: 0;
  color: rgba(0, 0, 0, 0.5) !important;
}

.bsxui-icon-button {
  display: flex !important;
  align-items: center;
}

.bsxui-config-panel-row {
  margin-bottom: 24px;
}

.bsxui-config-panel-img {
  max-width: 100%;
  height: auto;
}

.bsxui-config-panel-text {
  font-size: 13px;
}

.bsxui-config-panel-img-button {
  display: block;
  height: auto;
  padding: 0;
  border: 0;
  border-radius: 0;
  color: #dddddd !important;
  background-image: repeating-linear-gradient(-45deg, currentColor 25%, transparent 25%, transparent 75%, currentColor 75%, currentColor), repeating-linear-gradient(-45deg, currentColor 25%, transparent 25%, transparent 75%, currentColor 75%, currentColor);
  background-position: 0 0, 10px 10px;
  background-size: 20px 20px;
}
.bsxui-config-panel-img-button.has-margin-bottom {
  margin-bottom: 12px;
}

.bsxui-in-widget-overlay-panel {
  position: absolute;
  width: 100%;
  padding: 0.5rem 0.25rem;
  text-align: center;
}
.bsxui-in-widget-overlay-panel.bsxui-top {
  top: 0;
}
.bsxui-in-widget-overlay-panel.bsxui-bottom {
  bottom: 0;
}

.bsxui-initial-inline-control {
  background-color: rgba(238, 238, 238, 0.5);
  width: 100%;
}

.bsxui-initial-inline-control-heading {
  text-align: left;
  padding: 16px;
}

.bsxui-inline-control {
  background-color: rgba(238, 238, 238, 0.5);
  width: 100%;
  border: 1px dashed var(--wp-admin-theme-color);
  padding: 0.25rem;
  text-align: left;
}
.bsxui-inline-control *:not[class^=bsxui-] {
  margin-bottom: 0;
}

.bsxui-icon-text-button-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-bottom: 24px;
}
.bsxui-icon-text-button-list .bsxui-icon-text-button-list-item {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: auto;
  cursor: pointer;
}
.bsxui-icon-text-button-list .bsxui-icon-text-button-list-item.active {
  color: var(--wp-admin-theme-color);
}
.bsxui-initial-inline-control .bsxui-icon-text-button-list .bsxui-icon-text-button-list-item {
  width: 7.5rem;
}

.bsxui-inline-editor {
  display: inline-block;
  width: auto;
}

.wp-block[data-display=inline-block] {
  display: inline-block;
}

.wp-block[data-vertical-align=middle] {
  vertical-align: middle;
}